/* src/App.css */
/* Reset or normalize CSS can go here if needed */

.App {
  font-family: 'Inter', Arial, sans-serif;
  /* Example using Inter, fallback to Arial and sans-serif */
}

header,
nav,
/* footer {
  text-align: center;
  padding: 20px;
