main {
  padding: 20px;
}

section {
  margin: 20px 0;
}

footer {
  background-color: #ebecf2;
  /* Light gray background */
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
  align-items: center;
  height: 20vh;
  /* Adjust height as needed */
  padding: 40px;
  box-sizing: border-box;
}

.contact a {
  text-decoration: none;
  color: black;
  /* Ensure good contrast with the light background */
  margin: 0 1rem;
  cursor: pointer;
  background: none;
  border: none;
}