.about-grid {
  background-color: #ebecf2; /* Light gray background */
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  align-items: center;
  min-height: 80vh; /* Changed height to min-height for responsiveness */
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.5s ease, transform 0.5s ease;
}

.about-grid.visible {
  opacity: 1;
  transform: translateY(0);
}

.about-text {
  padding: 20px;
}

.about-text h1 {
  font-size: 48px;
  margin-bottom: 20px;
}

.about-text h1 span {
  font-family: 'Arial', Helvetica, sans-serif; /* Ensure fallback fonts for cross-compatibility */
  font-size: 48px;
  margin-bottom: 20px;
  color: transparent;
  -webkit-text-stroke: 0.5px #ffffff; /* Text stroke effect */
  background: url("../assets/back.png"); /* Adjust path relative to your CSS file */
  -webkit-background-clip: text;
  background-position: 0 0;
  animation: back 25s linear infinite; /* Animation for background */
}

@keyframes back {
  100% {
    background-position: 1000px 0; /* Adjust animation distance as needed */
  }
}

.about-text p {
  font-size: 18px;
  margin-bottom: 20px;
}

.download-button {
  display: inline-block;
  padding: 10px 20px;
  background-color: #0073b1;
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.download-button:hover {
  background-color: #053a57;
}

.about-image {
  background-color: rgba(128, 211, 255, 0.199); /* Adjusted background color with opacity */
  width: 75%;
  height: 80vh; /* Adjusted height */
  overflow: hidden; /* Ensuring image stays within bounds */
}

.about-image img {
  width: 100%;
  height: 100%; /* Ensure image covers the container */
  object-fit: cover; /* Maintain aspect ratio */
  object-position: top; /* Position image at the top */
}
