/* src/components/Skills.css */

#skills {
    padding: 20px;
    text-align: center;
}

.skills-container {
    display: flex;
    justify-content: center; /* Center align skill cards */
    margin-top: 20px;
}

.skill-card {
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 40px;
    margin-right: 30px; /* Space between cards */
    flex: 0 0 auto; /* Prevent shrinking */
}

.skill-card:last-child {
    margin-right: 0; /* Remove margin from last card */
}

.skill-icon {
    width: 60px; /* Adjust icon size to a more appropriate value */
    height: 60px; /* Maintain aspect ratio */
    object-fit: contain; /* Ensure the image fits within the dimensions */
}

.navigation-buttons {
    display: flex;
    justify-content: center; /* Center align arrows */
    margin-top: 20px;
}

.arrow {
    font-size: 24px; /* Adjust size of arrow */
    margin: 0 20px; /* Space between arrows */
    cursor: pointer; /* Change cursor to pointer */
}

.progress-indicator {
    display: flex;
    justify-content: center; /* Center align dots */
    margin-top: 10px;
}

.dot {
    height: 10px;
    width: 10px;
    margin-right: 5px;
    background-color: #bbb; /* Inactive dot color */
    border-radius: 50%;
}

.dot.active {
    background-color: #007bff; /* Active dot color */
}