/* src/components/Contact.css */

#contact {
    padding: 20px;
    text-align: center;
}

.contact-info {
    display: flex;
    justify-content: space-around; /* Space out contact items */
    margin-top: 20px;
}

.contact-item {
    flex: 1;
    margin: 0 10px; /* Space between items */
}

.contact-item h2 {
    font-size: 1.5em;
    margin-bottom: 10px;
}

.contact-item p {
    font-size: 1em;
}